/* Grid */
html {
  height: 100%; }

body {
  max-width: 120rem;
  margin: auto;
  padding: 0 2rem;
  height: 100%; }

.app {
  height: 100%; }

.main {
  margin-top: 64px; }

.row {
  margin-right: 0rem;
  margin-left: 0rem;
  padding: 5rem 0 5rem 0; }

/* fonts */
html {
  font-family: "Sofia Pro", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, "Helvetica Neue", sans-serif;
  font-size: 8px;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

h1 {
  font-size: 5rem;
  font-weight: 900;
  letter-spacing: -2px;
  margin: 0;
  padding: 0 0 2rem 0; }

h2 {
  font-size: 4.5rem;
  font-weight: 900;
  margin: 0;
  padding: 0 0 2rem 0; }

h3 {
  font-size: 3.25rem;
  font-weight: 900;
  margin: 0;
  padding: 0 0 2rem 0; }

p {
  font-size: 1.75rem;
  line-height: 4rem;
  margin: 0;
  padding: 0 0 1rem 0; }

p.body-large {
  font-size: 3rem;
  padding: 0 0 4rem 0; }

a {
  color: #273653;
  font-size: 1.8rem;
  font-weight: 700;
  text-decoration: none; }

/* Colors */
html {
  color: #273653; }

/* Buttons */
.primary-button {
  display: inline-block;
  padding: 2rem 6rem;
  background-color: #0055ff;
  color: white;
  border: none;
  border-radius: 3rem;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  font-size: 2rem;
  font-weight: 700;
  -webkit-transition-property: all;
  transition-property: all;
  -webkit-transition-duration: 0.2s;
          transition-duration: 0.2s;
  -webkit-transition-timing-function: ease;
          transition-timing-function: ease;
  -webkit-transition-delay: 0s;
          transition-delay: 0s; }
  .primary-button:hover {
    background-color: #0065FF; }
  .primary-button:focus {
    outline: none; }

/* Templates */
.rectangle {
  display: none;
  max-width: 280px;
  border-radius: 5rem;
  margin: 0 auto;
  padding: 1.25rem; }

.screen video {
  max-width: 100%;
  border-radius: 4rem; }

.shadow {
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.1), 0 3px 6px rgba(0, 0, 0, 0.05), 0 10px 20px rgba(0, 0, 0, 0.1); }

/* Over 768px (sm) */
@media only screen and (min-width: 48em) {
  body {
    padding: 0 4rem; }

  h1 {
    font-size: 6rem;
    font-weight: 900;
    margin: 0;
    padding: 0 0 2rem 0; }

  h2 {
    font-size: 6rem;
    font-weight: 900;
    margin: 0;
    padding: 0 0 2rem 0; }

  h3 {
    font-size: 4.5rem;
    font-weight: 900;
    margin: 0;
    padding: 0 0 2rem 0; } }
/* Over 992px (md) */
@media only screen and (min-width: 62em) {
  h1 {
    font-size: 9rem;
    font-weight: 900;
    margin: 0;
    padding: 0 0 2rem 0; }

  h2 {
    font-size: 7rem;
    font-weight: 900;
    margin: 0;
